.carousel-material {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 150px;
  perspective: 500px;
  transform-style: preserve-3d;
  transform-origin: 0% 50%;
  margin-bottom: -60px;
  @media (min-width: 425px) {
    height: 300px;
    margin-bottom: -120px;
  }
  &.carousel-slider {
    top: 0;
    left: 0;
    .carousel-fixed-item {
      &.with-indicators {
        bottom: 68px;
      }
      position: absolute;
      left: 0;
      right: 0;
      bottom: 20px;
      z-index: 1;
    }
    .carousel-material-item {
      width: 100%;
      height: 100%;
      min-height: 150px;
      position: absolute;
      top: 0;
      left: 0;
      @media (min-width: 425px) {
        min-height: 300px;
      }
    }
  }
  .carousel-material-item {
    visibility: hidden;
    width: 150px;
    height: 150px;
    position: absolute;
    top: 0;
    left: 0;
    @media (min-width: 425px) {
      width: 300px;
      height: 300px;
    }
    & > img {
      width: 100%;
    }
  }
  // Materialbox compatibility
  &.scrolling .carousel-material-item .materialboxed,
  .carousel-material-item:not(.active) .materialboxed {
    pointer-events: none;
  }
}
