    transform: translate(20%, 100%);
    position: relative;
    z-index: 1; }
    .strip span:before {
      content: '';
      position: absolute;
      z-index: 1;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transform: translateY(-40%);
      transition: transform 0.6s cubic-bezier(0.65, 0.02, 0.23, 1); }
