.section-domicilios {
  padding-top: 3rem;
  padding-bottom: 3rem;
}
.domicilios {
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: auto;
  grid-template-areas: "item-1 item-2 item-3 item-4";
  @media (max-width: 767.98px) {
    grid-template-columns: repeat(2, 50%);
    grid-template-rows: auto;
    grid-template-areas:
    "item-1 item-3"
    "item-2 item-4";
  }
  .items {
    // display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.5s;
    cursor: pointer;
    &:hover {
      @media (min-width: 992px) {
        transform: scale(1.1);
        z-index: 1;
      }
    }
    .link-items {
      line-height: 0;
    }
    &.item-1 {
      grid-area: item-1;
    }
    &.item-2 {
      grid-area: item-2;
    }
    &.item-3 {
      grid-area: item-3;
    }
    &.item-4 {
      grid-area: item-4;
      position: relative;
      .content-text {
        position: absolute;
        width: 50%;
        top: 50%;
        left: 50%;
        text-align: center;
        padding: 0;
        transform: translate(-50%, -50%);
        @media (min-width: 768px) {
          width: 60%;
        }
        @media (min-width: 992px) {
          width: 60%;
        }
        @media (min-width: 1024px) {
          padding: 0 1rem;
          width: 70%;
        }
        @media (min-width: 1200px) {
          width: 70%;
        }
        @media (min-width: 1400px) {
          width: 60%;
        }
        h2 {
          margin-bottom: 0.5rem;
          font-size: 1.2rem;
          line-height: 1;
          color: $white;
          @media (min-width: 768px) {
            font-size: 1rem;
          }
          @media (min-width: 992px) {
            font-size: 1.2rem;
          }
        }
        p {
          margin-bottom: 0;
          font-size: 0.8rem;
          line-height: 1;
          color: $white;
          @media (min-width: 768px) {
            font-size: 0.6rem;
          }
          @media (min-width: 992px) {
            font-size: 0.8rem;
          }
        }
      }
    }
    img {
      max-width: 100%;
      width: 100%;
    }
  }
}