.section-welcome {
  background-image: url(../images/bg-welcome.png);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  padding-top: 100px;
  display: flex;
  align-items: center;
  @media (min-width: 992px) {
    padding-top: 150px;
  }
  .content-text {
    text-align: center;
    & > * {
      margin: 0;
      margin-bottom: 1rem;
    }
  }
}
.strip {
  opacity: 0;
  span {
    display: inline-block;
    transition: transform 0.6s cubic-bezier(0.65, 0.02, 0.23, 1);
    transform: translate(20%, 100%);
    position: relative;
    z-index: 1;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transform: translateY(-40%);
      transition: transform 0.6s cubic-bezier(0.65, 0.02, 0.23, 1);
    }
    &.line {
      overflow: hidden;
      line-height: 1.5;
      display: block;
      transform: none;
      &:before {
        display: none;
      }
    }
  }
  .animate {
    transform: translate(0, 0);
    &:before {
      transform: translateY(100%);
    }
  }
}
.section-welcome2 {
  height: calc(75px + 0rem);
  background-color: $white;
  @media (min-width: 425px) {
    height: calc(150px + 0rem);
  }
}