.section-nosotros {
  background-color: $white;
  padding-top: 3rem;
  padding-bottom: 3rem;
  .content-imgs {
    position: relative;
    min-height: 310px;
    @media (min-width: 425px) {
      min-height: 410px;
    }
  }
  .outer-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
  }
  .img-container {
    transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotateX(45deg);
    -o-transform: rotateX(45deg);
    position: relative;
    height: 230px;
    width: 230px;
    margin: 0 auto;
    padding: 0;
    @media (min-width: 425px) {
      height: 300px;
      width: 300px;
    }
    .img-rhombus {
      overflow: hidden;
      height: 44%;
      width: 44%;
      display: inline-block;
      margin: 2%;
      padding: 0;
      border-radius: 10px;
      .inner-wrapper {
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
        width: 160%;
        height: 160%;
        transition: all 0.5s linear;
        &:hover {
          width: 200%;
          height: 200%;
        }
        img {
          transform: rotate(-45deg);
          -ms-transform: rotate(-45deg);
          -webkit-transform: rotate(-45deg);
          -moz-transform: rotateX(-45deg);
          -o-transform: rotateX(-45deg);
          width: 100%;
          height: 100%;
          margin: 0;
          padding: 0;
        }
      }
    }
  }
  .vineta-container {
    position: absolute;
    height: 260px;
    width: 260px;
    margin: 0 auto;
    padding: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @media (min-width: 425px) {
      height: 340px;
      width: 340px;
    }
    .img-vineta {
      overflow: hidden;
      height: 17%;
      width: 17%;
      display: inline-block;
      margin: 2%;
      padding: 0;
      transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      -webkit-transform: rotate(45deg);
      -moz-transform: rotateX(45deg);
      -o-transform: rotateX(45deg);
      position: absolute;
      &.vineta1 {
        top: 0;
        left: 0;
      }
      &.vineta2 {
        top: 0;
        right: 0;
      }
      &.vineta3 {
        bottom: 0;
        left: 0;
      }
      &.vineta4 {
        bottom: 0;
        right: 0;
      }
      .inner-wrapper {
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        transition: all 0.5s linear;
        &:hover {
          width: 200%;
          height: 200%;
        }
        img {
          width: 100%;
          height: 100%;
          margin: 0;
          padding: 0;
        }
      }
    }
  }
  .rosco {
    position: absolute;
    height: 260px;
    width: 260px;
    margin: 0 auto;
    padding: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @media (min-width: 425px) {
      height: 340px;
      width: 340px;
    }
    img {
      width: 100%;
    }
  }
}