$carousel-material-height-mb: 150px !default;
$carousel-material-height: 300px !default;
.content-slider {
  position: relative;
  .carousel-material {
    .carousel-material-item {
      background-size: cover;
      background-position: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      transition: transform 0s ease-in-out;
      img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 15px;
        z-index: -1;
        transition: .5s ease;
        object-fit: cover;
      }
      .placa-slider {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 15px;
        background: linear-gradient(180deg, rgba(255, 255, 255, 1) 0, rgba(255, 255, 255, 0.9) 20%, rgba(255, 255, 255, 0) 40%);
      }
      .title-slider {
        position: absolute;
        top: 1rem;
        left: 0;
        width: 100%;
        color: $blue;
        padding: 0 1rem;
        font-size: 2rem;
        text-align: center;
      }
      .btn-ver {
        width: 40%;
        height: 40%;
        position: absolute;
        bottom: -1px;
        right: 0;
        background-color: $blue;
        clip-path: polygon(100% 0%, 0% 100%, 100% 100%);
        border-bottom-right-radius: 15px;
        display: flex;
        align-items: flex-end;
        justify-content: end;
        padding: 0.5rem;
        @media (min-width: 425px) {
          width: 30%;
          height: 30%;
        }
        a {
          color: $white;
          font-size: 1.5rem;
          text-decoration: none;
          transition: 0.3s;
          &:hover {
            color: $yellow;
          }
        }
      }
    }
  }
  .carousel-material-prev,
  .carousel-material-next {
    display: none;
    width: 40px;
    height: 40px;
    padding: 0;
    border: 0;
    background: transparent;
    position: absolute;
    top: 50%;
    opacity: 0.5;
    transform: translateY(-50%);
    transition: all 0.3s;
    &:hover {
      opacity: 1;
    }
    @media (min-width: 1200px) {
      display: block;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  .carousel-material-prev {
    left: 1rem;
  }
  .carousel-material-next {
    right: 1rem;
  }
}
